footer {
  background-color: #fff5f6;
}
.container-footer {
  padding-top: 113px;
  padding-bottom: 25px;
  @media screen and (min-width: 768px) {
    padding-top: 165px;
    padding-bottom: 18px;
  }
  @media screen and (min-width: 1280px) {
    padding-top: 165px;
    padding-bottom: 10px;
  }
}

.footer-middle {
  margin-bottom: 43px;
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin-bottom: 91px;
  }
  @media screen and (min-width: 1280px) {
    margin-bottom: 97px;
    align-items: flex-end;
  }
}

.footer-left {
  @media screen and (max-width: 767px) {
    margin-bottom: 43px;
  }
}

.footer__before-text {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 18px;
  line-height: calc (21 / 18);
  text-transform: uppercase;
  color: $secondary-accent;
  margin-bottom: 17px;
  width: 232px;
  @media screen and (min-width: 768px) {
    margin-bottom: 15px;
    font-size: 26px;
    line-height: calc(30 / 26);
    letter-spacing: 0.04em;
    width: 340px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 30px;
    line-height: calc(34 / 30);
    width: 477px;
  }
}

.footer__title {
  font-family: Titan One;
  font-style: normal;
  font-weight: normal;
  font-size: 34px;
  line-height: calc(39 / 34);
  letter-spacing: 0.04em;
  text-transform: uppercase;
  color: $main-accent;
  width: 200px;
  @media screen and (min-width: 768px) {
    font-size: 48px;
    line-height: calc(55 / 48);
    letter-spacing: 0.06em;
    width: 340px;
  }
  @media screen and (min-width: 1280px) {
    font-size: 58px;
    line-height: calc(66 / 58);
    width: 477px;
  }
}

.footer__socials {
  display: flex;
  justify-content: end;
  margin-bottom: 46px;
}

.footer__socials-item {
  &:not(:last-child) {
    margin-right: 29px;
  }
}

.footer__socials-link {
  width: 34px;
  height: 34px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  background: #fea5bb;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover,
  &:focus {
    background-color: $main-accent;
  }
}

.footer__text {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 12px;
  line-height: calc(22 / 12);
  text-align: right;
  color: #000000;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26 / 14);
  }
  @media screen and (min-width: 1280px) {
    font-size: 16px;
    line-height: calc(30 / 16);
    letter-spacing: 0.02em;
  }
}

.contacts__link {
  color: $main-accent;
}

.footer-bottom {
  padding-top: 22px;
  border-top: 1px solid #e1e1e1;
  @media screen and (min-width: 768px) {
    display: flex;
    justify-content: center;
    padding-top: 29px;
  }
}

.footer__text-rights {
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: calc(21 / 14);
  text-align: center;
  color: $text-color;
  @media screen and (min-width: 768px) {
    font-size: 14px;
    line-height: calc(26 / 14);
    letter-spacing: 0.02em;
    &:not(:last-child) {
      margin-right: 5px;
    }
  }
}
