.mobile-menu {
  padding: 60px 0;
  position: fixed;
  top: 0;
  right: 0;
  width: 250px;
  height: 100vh;
  display: flex;
  flex-direction: column;
  background-color: #e17992;
  z-index: 99;
  overflow: hidden;
  transform: translateX(100%);
  transition: transform 500ms cubic-bezier(0.4, 0, 0.2, 1);
  @media screen and (min-width: 1200px) {
    display: none;
  }
}

.mobile-menu.mobile-menu__show {
  transform: translateX(0);
}
.mobile-menu__button-close {
  position: absolute;
  top: 23px;
  right: 28px;
  display: flex;
  border: none;
  background-color: transparent;
}

.mobile-menu__icon-close {
  stroke: #ffffff;
  transition: stroke 100ms cubic-bezier(0.8, 0.4, 0.3, 0.9);
  &:hover,
  &:focus {
    stroke: #d41443;
    cursor: pointer;
  }
}

.mobile-menu__button {
  display: flex;
  align-items: center;
  align-content: center;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  border-radius: 22px;
  margin-bottom: 20px;
  border: none;
  cursor: pointer;
  width: 209px;
  height: 44px;
  color: #d41443;
  background-color: #ffffff;
  font-family: DM Sans;
  font-style: normal;
  font-weight: bold;
  font-size: 16px;
  line-height: 1.31px;
  &:focus,
  &:hover {
    background-color: #ecebeb;
    transition: background-color 250ms linear;
  }
  @media screen and (min-width: 768px) {
    width: 186px;
    height: 44px;
  }
}
.mobile-menu__list {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.3;
}
.mobile-menu__link {
  padding: 11px 10px 13px;
  color: #ffffff;
  transition: color 350ms cubic-bezier(0.8, 0.4, 0.3, 0.9);
  &:hover,
  &:focus {
    color: #d41443;
  }
}

.mobile-menu__item {
  border-top: 1px solid #e18298;
  padding: 11px 10px 13px;
  &:last-child {
    border-bottom: 1px solid #e18298;
  }
}

.mobile-menu__icon {
  margin-left: 20px;
  fill: #d41443;
  cursor: pointer;
}
